<template lang="html">
  <div class="Review-contract">
    <Row type="flex" justify="space-around" class="code-row-bg search_criteria">
        <Col span="4" class="font-16">
          <h3>{{title}}</h3>
        </Col>
        <Col span="5">

        </Col>
        <Col span="5">

        </Col>
        <Col span="8">
          <Button type="primary" class="mgl-20" @click="succ"v-if="active !=2">{{btnName}}</Button>
          <Button type="warning" class="mgl-20" @click="cancel" v-if="active ==1">{{$t("common.Overrule")}}</Button>
          <Button type="warning" class="mgl-20" @click="cancel" v-if="active ==2">{{$t("common.Overrule")}}</Button>
        </Col>
    </Row>
    <Row type="flex" justify="space-between" class="code-row-bg search-review">
        <Col span="6">
          <span>{{$t("common.Contract_No")}}：</span>
          <span>DG20180302001</span>
        </Col>
        <Col span="10">
          <span>{{$t("common.Cooperative_company_name")}}：</span>
          <span>深圳怡亚通股份有限公司</span>
        </Col>
        <Col span="5">
          <span>{{$t("common.Submission_time")}}：</span>
          <span>2018-3-12  10:30:20</span>
        </Col>
    </Row>
    
    <Row type="flex" justify="space-between" class="code-row-bg contract-row">
        <Col span="6">
          <span>{{$t("common.Contract_name")}}：</span>
          <span>战略合作协议</span>
        </Col>
        
    </Row>
    <Row type="flex" justify="start" class="code-row-bg text-edit">
      <Col span="3" class="text-left mgl-30" >{{$t("common.Contract_content")}}：</Col>
      <Col :md="18" :lg="14" class="mgl-20">
        <div ref="editor" style="text-align:left"><p>{{content }}</p></div>
        <!-- <button @click="getContent">查看内容</button> -->
      </Col>
    </Row>
    
  </div>
</template>

<script>
import E from 'wangeditor'
export default {
  data (){
    return {
      btnName:this.$t("common.Adopt"),
      ContractUse:'',
      editorContent: '',
      title:'待审核详情',
      active:1,
      content:'一、特别提示在此特别提醒您（用户）在注册成为***用户之前，请认真阅读本《***用户服务协议》（以下简称“协议”），确保您充分理解本协议中各条款。请您审慎阅读并选择接受或不接受本协议。除非您接受本协议所有条款，否则您无权注册、登录或使用本协议所涉服务。您的注册、登录、使用等行为将视为对本协议的接受，并同意接受本协议各项条款的约束。  本协议约定***与用户之间关于“***”服务（以下简称“服务”）的权利义务。“用户”是指注册、登录、使用本服务的个人。本协议可由***随时更新，更新后的协议条款一旦公布即代替原来的协议条款，恕不再另行通知，用户可在本APP中查阅最新版协议条款。在修改协议条款后，如果用户不接受修改后的条款，请立即停止使用***提供的服务，用户继续使用***提供的服务将被视为接受修改后的协议。二、账号注册1、用户在使用本服务前需要注册一个“***”账号。“***”账号应当使用手机号码绑定注册，请用户使用尚未与“***”账号绑定的手机号码，以及未被***根据本协议封禁的手机号码注册“***”账号。***可以根据用户需求或产品需要对账号注册和绑定的方式进行变更，而无须事先通知用户。  2、如果注册申请者有被***封禁的先例或涉嫌虚假注册及滥用他人名义注册，及其他不能得到许可的理由， ***将拒绝其注册申请。  3、鉴于“***”账号的绑定注册方式，您同意***在注册时将允许您的手机号码及手机设备识别码等信息用于注册。  4、在用户注册及使用本服务时，***需要搜集能识别用户身份的个人信息以便***可以在必要时联系用户，或为用户提供更好的使用体验。***搜集的信息包括但不限于用户的姓名、地址；***同意对这些信息的使用将受限于第三条用户个人隐私信息保护的约束。'
    }
  },
  methods:{
    succ () {
      this.$Message.success(this.btnName);
      setTimeout(() =>{
        this.$router.push('/Partner_contract');
      },2000)
    },
    getContent: function () {
        alert(this.editorContent)
    },
    getParams () {
        // 取到路由带过来的参数
        let routerParams = this.$route.params.send_data
        // 将数据放在当前组件的数据内
        if(routerParams){
          this.active = routerParams
        }
    },
    cancel () {
      this.$router.push('/Partner_contract');
    }
  },
  mounted() {
    var editor = new E(this.$refs.editor)
    editor.customConfig.onchange = (html) => {
      //this.editorContent = html
    }
    editor.create()
    this.getParams ()
    if(this.active == 1){
      this.title = this.$t("common.Pending_details")
    }else if(this.active == 2){
      this.title = this.$t("common.Audit_pass_details")
      this.btnName = '驳回'
    }else if(this.active == 3){
      this.title = this.$t("common.Rejected_details")
    }
  }
}
</script>

<style lang="css">
.Review-contract{
  background-color: #fff;
  padding: 20px;
}
.Review-contract .search_criteria{
  height: 50px;
  background-color: rgba(242, 242, 242, 1);
  line-height: 50px;
  text-align: center;
}
.Review-contract .contract-row{
  line-height: 50px;
  margin-bottom: 20px;
  text-align: left;
  margin-left: 30px;
}
.Review-contract .ivu-select-dropdown{
  z-index: 99999
}
.Review-contract .search-review{
  margin-top: 20px;
  text-align: left;
  margin-left: 30px;
}
</style>



